header .navbar {
	background: -webkit-gradient(linear, left top, right top, from(#1c2a48), to(#2e3951));
	background: -webkit-linear-gradient(left, #1c2a48, #2e3951);
	background: -o-linear-gradient(left, #1c2a48, #2e3951);
	background: linear-gradient(to right, #1c2a48, #2e3951);
	padding: 0;
}

header .brand { font-size: 28px;}

header ul { margin: 0;}

header li {
	list-style-type: none;
	display: inline;
	color: white;
	padding: 0 15px;
	font-size: 20px;
}

header a { padding: 10px; color: white;}
header a:hover { color: #33b5e5;}

header .view {
	background: url("./bg.jpg");
	background-size: cover;
	height: 400px;
}

header .view h1 {
	color: white;
	letter-spacing: 5px;
	font-weight: bold;
	margin-top: 180px;
}

header .view h3 {
	color: #ccc;
}


